  <div class="monetary-debt">
    <h2>Monetary debt</h2>
    <my-list :users="users" />
    <UI-button @click="removeItems" bgColor="#f46161">Remove</UI-button>
  </div>
</template>

<script>
import MyList from "@/components/MyList.vue";
import UIButton from "@/components/UI/UIButton.vue";
export default {
  components: {
    MyList,
    UIButton,
  },
  props: {
    gel: Number,
    users: Array,
  },
  methods: {
    removeItems() {
      this.users.forEach((user) => {
        user.debt = 0;
      });
    },
  },
};
</script>

