<template>
  <div class="exr-wrapper">
    <h2>GEL exchange rate</h2>
    <UI-input v-model="inputData" placeholder="Your expenses" />
    <div class="exr-wrapper__rate">
      <p>🇷🇺 {{ (rub * inputData).toFixed(2) }}</p>
      <p>🇰🇿 {{ (kzt * inputData).toFixed(2) }}</p>
    </div>
  </div>
</template>

<script>
import UIInput from "@/components/UI/UIInput.vue";
import { getUsers } from "@/apiService";

export default {
  data() {
    return {
      inputData: 1,
      kzt: 0,
      rub: 0,
    };
  },
  components: {
    UIInput,
  },
  methods: {
    fetchData() {
      getUsers()
        .then((response) => {
          this.rub = response.data.conversion_rates.RUB;
          this.kzt = response.data.conversion_rates.KZT;
        })
        .catch((error) => {
          console.error(error);
        });
    },
  },
  mounted() {
    this.fetchData();
  },
};
</script>

